import { useEffect, useState } from 'react';
import axios from 'axios';

export default function Orders() {
  const [orders, setOrders] = useState([]);
  const [statusFilter, setStatusFilter] = useState('');

  useEffect(() => {
    const token = localStorage.getItem('token');
    axios.get('http://localhost:4000/admin/orders', {
      headers: { Authorization: `Bearer ${token}` },
      params: statusFilter ? { status: statusFilter } : {}
    }).then(res => setOrders(res.data.items));
  }, [statusFilter]);

  return (
    <div>
      <h1>Orders</h1>
      <select onChange={e => setStatusFilter(e.target.value)}>
        <option value="">All</option>
        <option value="paid">Paid</option>
        <option value="pending">Pending</option>
        <option value="refunded">Refunded</option>
      </select>
      <table>
        <thead>
          <tr><th>ID</th><th>User</th><th>Total</th><th>Status</th><th>Actions</th></tr>
        </thead>
        <tbody>
          {orders.map(order => (
            <tr key={order.id}>
              <td>{order.id}</td>
              <td>{order.user.email}</td>
              <td>{order.total}</td>
              <td>{order.status}</td>
              <td>
                <button onClick={() => updateStatus(order.id, 'refunded')}>Refund</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );

  async function updateStatus(id, status) {
    const token = localStorage.getItem('token');
    await axios.patch(`http://localhost:4000/admin/orders/${id}`, { status }, {
      headers: { Authorization: `Bearer ${token}` }
    });
    setOrders(prev => prev.map(o => o.id === id ? { ...o, status } : o));
  }
}
